.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.2);
  visibility: visible;
  transition: 0.3s;
  z-index: 10;
}

.modal {
  position: relative;
  padding: 0 16px;
  opacity: 1;
  transition: opacity 0.3s;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  width: 500px;
  height: 450px;
  background: #4ca1af; /* fallback for old browsers */
  background: linear-gradient(to right, #c4e0e5, #4ca1af);
  z-index: 100; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
